.footersection {
  position: fixed;
  bottom:0vh;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  z-index: 0;
}
.col {
  background-color: var(--color-black);
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 23px;
  padding:5px 0px ;
}
.svgbox {
  width: 50px;
  border: 3px solid var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
.svg {
  fill: var(--white);
  stroke: var(--white);
  width: 30px;
}
a{
  color: var(--white);
  text-decoration: none;
}