.header {
  margin: 10px;
  display: grid;
  grid-template-columns: 1fr 8fr 1fr;
}
.proimage {
  display: flex;
  justify-content: center;
  border-radius: 50%;
  border: 3px solid var(--white);
  padding: 10px;
  width: 55px;
}
.adpro {
  fill: var(--white);
  width: 40px;
}
.name {
  display: flex;
  justify-content: center;
  padding: 10px;
  font-size: 40px;
  font-family: "instagram";
}
.menu {
  display: flex;
  justify-content: center;
  height:40px ;
}
.menu button {
  padding: 7px;
  margin: 0px 10px;
  width: 20%;
  background-color: rgb(36, 135, 222);
  color: var(--white);
  font-size: large;
  font-weight: 600;
  border-radius: 10px;
  border: none;
  cursor: pointer;
}
.menu button:hover,button:active{
    background-color: rgb(36, 135, 222);
    border: 1px solid rgb(40, 177, 219);
    box-shadow: 0px 0px 5px rgb(40, 177, 219);
}
.content{
  padding-bottom: 63px;
}