.section {
  display: grid;
  width: 700px;
  margin: 10px;
  position: relative;
  top: 10px;
}
.header {
  margin: 10px;
  display: grid;
  grid-template-columns: 1fr 10fr 2fr;
  justify-content: center;
  height: 50px;
}
.profile {
  display: grid;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 50px;
}
.profile img {
  width: 40px;
  border-radius: 50%;
}
.username {
  display: grid;
  align-content: center;
  padding-left: 10px;
  text-align: left;
  font-size: 20px;
  font-weight: 600;
}
.icon {
  display: grid;
  justify-content: right;
  align-content: center;
  height: 50px;
}
.menuicon {
  width: 25px;
  height: 25px;
  fill: var(--white);
  transform: rotate(90deg);
}
.userprofile {
  float: left;
}
.image {
  display: grid;
  justify-content: left;
  width: 700px;
}
.image img {
  width: 700px;
  max-width: 700px;
}
.footer {
  text-align: left;
  margin: 10px;
}
.iconbar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 8fr;
  justify-content: center;
}
.icol {
  text-align: center;
}
.like {
  width: 35px;
  height: 35px;
  fill: var(--white);
  margin: 5px;
}
.libutton {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.libutton:active,.libutton:hover{
  background-color: transparent;
  border: none;
  box-shadow: none;
}
.comico {
  width: 34px;
  height: 34px;
  fill: var(--white);
  margin: 5px;
  stroke-width: 0.4px;
  stroke: var(--white);
}
.share {
  width: 35px;
  height: 35px;
  fill: var(--white);
  margin: 5px;
}
.likedcount {
  margin: 10px;
  margin-left: 0px;
}
.comments {
  margin: 10px;
  margin-left: 0px;
}
.likedcount span {
  font-weight: 700;
}
.comments span {
  font-weight: 700;
}
.liche {
  fill: rgb(235, 30, 30);
  width: 35px;
  height: 35px;
  margin: 5px;
  animation: liked 0.27s linear;
}
@keyframes liked {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.8);
  }
  100% {
    transform: scale(1);
  }
}
@media screen and (max-width: 700px) {
  .section {
    width: 500px;
    margin: 5px;
  }
  .header {
    width: 500px;
    margin: 5px;
  }
  .image img {
    margin: 5px;
    width: 500px;
  }
  .footer {
    margin: 5px;
    width: 500px;
  }
}
