.login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.loginbox {
  display: grid;
  width: 500px;
  height: 400px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 1px rgb(31, 214, 234);
  background-color: var(--color-black);
  align-content: center;
}
.table {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 4fr;
  row-gap: 10px;
}
.row {
  display: flex;
  justify-content: center;
  margin: 20px 20px;
  width: 80%;
}
.col {
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  width: 100%;
  font-weight: 600;
}
div[data="user"] {
  display: block;
  text-align: right;
}
input {
  background-color: var(--white);
  outline: none;
  border: none;
  border-radius: 5px;
  height: 30px;
  font-weight: 600;
  width: 100%;
  font-size: 16px;
  padding: 0px 10px;
}
h2 {
  margin: 0px;
}
.submit {
  width: max-content;
  border: none;
  outline: none;
  background-color: rgb(12, 137, 23);
  color: var(--white);
  font-size: 20px;
  border-radius: 5px;
  padding: 10px;
  margin: 5px;
  border: 2px solid transparent;
}
.submit:hover,
.submit:active {
  cursor: pointer;
  border: 2px solid rgb(12, 137, 23);
  background-color: rgb(34, 154, 44);
  box-shadow: 0px 0px 2px 1px rgb(34, 154, 44);
}
.signup{
  width: max-content;
  border: none;
  outline: none;
  background-color: rgb(28, 171, 200);
  color: var(--white);
  font-size: 20px;
  border-radius: 5px;
  padding: 10px;
  margin: 5px;
  border: 2px solid transparent;
}
.signup:hover,
.signup:active {
  cursor: pointer;
  border: 2px solid rgb(23, 149, 174);
  background-color: rgb(28, 171, 200);
  box-shadow: 0px 0px 2px 1px rgb(28, 171, 200);
}
.row[data="warning"] {
  color: red;
  display: grid;
  justify-content: center;
  margin: 0px;
  padding: 0px;
}
.row[data="button"] {
  display: block;
  text-align: right;
}


