.form {
  text-align: center;
}
.input {
  display: flex;
  justify-content: center;
}
.input form {
  display: grid;
  grid-template-columns: 180px 180px 180px 180px;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  margin: 0px 10px;
  text-align: center;
  column-gap: 10px;
  row-gap: 10px;
  font-weight: 600;
  font-size: 20px;
  align-items: center;
}
.input input,
textarea {
  background-color: var(--white);
  border: 2px solid transparent;
  border-radius: 10px;
  height: 30px;
  font-size: 15px;
  outline: none;
  margin: 10px;
}
.input input:focus,
textarea:focus {
  background-color: var(--white);
  border: 2px solid rgb(34, 218, 231);
}
.submitbox {
  grid-row: 3;
  grid-column: 2 / span 2;
  width: 100%;
}
.submit{
  width: max-content;
  border: none;
  outline: none;
  background-color: rgb(12, 137, 23);
  color: var(--white);
  font-size: 20px;
  border-radius: 5px;
  padding: 10px;
  margin: 5px;
  border: 2px solid transparent;
}
.submit:hover,.submit:active{
  cursor: pointer;
  border: 2px solid rgb(12, 137, 23);
  background-color: rgb(34, 154, 44);
  box-shadow: 0px 0px 2px 1px rgb(34, 154, 44);
  
}
