@font-face {
  font-family: "instagram";
  src: url("../../fonts/Instagram.otf");
}

.application {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 8fr 1fr 1fr;
  position: fixed;
  top: 0px;
  background-color: var(--color-black);
  z-index: 1;
  margin-top: 0px;
  padding: 1px;
}
.col {
  text-align: center;
  height: max-content;
  padding: 10px;
}
.logo {
  font-family: "instagram";
  font-size: 30px;
}
.logo h1 {
  font-size: 40px;
}
.camera {
  float: left;
  margin: 0px 10px;
}
.cameraicon {
  fill: var(--white);
  stroke: rgb(206, 206, 206);
  height: 35px;
  width: 35px;
}
.planeicon {
  fill: var(--white);
  stroke: rgb(206, 206, 206);
  height: 35px;
  width: 35px;
}
@media screen and (max-width: 700px) {
  .application {
    width: 90vw;
  }
}
