
.section {
  display: inline-block;
  width: max-content;
  border-radius: 50%;
  position: relative;
  margin: 25px;
  padding: 5px;
}
.photo {
  width: 70px;
  border-radius: 50%;
  border: 5px solid var(--color-black);
}
#photo {
  position: relative;
  width: 80px;
  height: 80px;
}
#photo::before {
  content: "";
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  background: linear-gradient(
    90deg,
    rgb(225, 136, 13),
    rgb(234, 85, 11),
    rgb(190, 23, 212)
  );
  border-radius: 50%;
  z-index: -1;
  animation: rotate 3s linear infinite;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.name {
  text-align: center;
  margin-top: 5px;
  width: 90px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
hr {
  border: 1px solid grey;
}
.list {
  display: grid;
  grid-auto-flow: column;
  overflow-x: auto;
  width: 100vw;
  padding-top:63px ;
}
.list::-webkit-scrollbar {
  display: none;
}
.online{
  position: absolute;
  top: 68px;
  left: 68px;
  width: 15px;
  box-shadow: 0px 0px 7px rgb(26, 211, 26);
  border-radius: 50%;
}
@media screen and (max-width: 700px) {
  .story {
    width: 90vw;
  }
  .section {
    width: 90vw;
  }
  .list{
    width: 90vw;
  }
}
